@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";


.c-type{
	// line-height: 1.5;
	// color:#ff5e00;
	//line height
	&.lh-0,.lh-0{
		line-height: 1 !important;
	}
	&.lh-1,.lh-1{
		line-height: 1.1 !important;
	}
	&.lh-2,.lh-2{
		line-height: 1.2 !important;
	}
	&.lh-3,.lh-3{
		line-height: 1.3 !important;
	}
	&.lh-4,.lh-4{
		line-height: 1.4 !important;
	}
	&.lh-5,.lh-5{
		line-height: 1.5 !important;
	}
	////////font-weight
	&.fw-400,.fw-400{
		font-weight: 400 !important;
	}
	&.fw-700,.fw-700{
		font-weight: 700 !important;
	}
	&.fw-900,.fw-900{
		font-weight: 900 !important;
	}
	////////margin
	// &.my-0,.my-0{
	// 	margin-top: 0 !important;
	// 	margin-bottom: 0 !important;
	// }
	// &.my-h1,.my-h1{
	// 	margin-top: 0.67em !important;
	// 	margin-bottom: 0.67em !important;
	// }
	// &.my-h2,.my-h2{
	// 	margin-top: 0.83em !important;
	// 	margin-bottom: 0.83em !important;
	// }
	// &.mb-24,.mb-24{
	// 	margin-bottom: 24px !important;
	// }
	// &.mb-56,.mb-56{
	// 	margin-bottom: 56px !important;
	// }
	// &.mb-16,.mb-16{
	// 	margin-bottom: 16px !important;
	// }
	// &.mb-title{
	// 	margin-bottom: 56px !important;
	// }
	&.fs-lg,.fs-lg{
		font-size: 56px;
	}
	///////algn
	&.ta-center,.ta-center{
		text-align:center;
	}
	///////letter spacing
	&.lp-0{
		letter-spacing: 0 !important;
	}
	&.lp-2{
		letter-spacing: 2px !important;
	}
	&.h1,.h1{
		font-size:24px !important;
		font-weight: 400;
		margin:0;
    	@media (min-width: map-get($grid-breakpoints, 'lg')){
			font-size:36px !important;
		}
	}
	&.h2,.h2{
		font-size:20px !important;
		font-weight: 400;
		margin:0;
    	@media (min-width: map-get($grid-breakpoints, 'lg')){
			font-size:28px !important;
		}
	}
	&.h3,.h3{
		font-size:18px  !important;
		font-weight: 400;
		margin:0;
    	@media (min-width: map-get($grid-breakpoints, 'lg')){
			font-size:24px  !important;
		}
	}
	&.h4,.h4{
		font-size:16px  !important;
		font-weight: 400;
		margin:0;
    	@media (min-width: map-get($grid-breakpoints, 'lg')){
			font-size:20px  !important;
		}
	}
	&.h5,.h5{
		font-size:16px  !important;
		font-weight: 400;
		margin:0;
    	@media (min-width: map-get($grid-breakpoints, 'lg')){
			font-size:18px  !important;
		}
	}
	&.h6,.h6{
		font-size:14px  !important;
		font-weight: 400;
		margin:0;
    	@media (min-width: map-get($grid-breakpoints, 'lg')){
			font-size:16px  !important;
		}
	}
	//width
	&.w-50p,.w-50p{
		width:50% !important;
	}

}
.cathay-type{
	
	@import "../common/reset.scss";

	.h1{
		font-size:36px;
	    letter-spacing: 3px;
	    max-width: 80%;
		margin-left: auto;
		margin-right: auto;
	}

	.h2{
		font-size:30px;
	    letter-spacing: 3px;
	    max-width: 90%;
		margin-left: auto;
		margin-right: auto;
		color:#666666;
    	@media (min-width: map-get($grid-breakpoints, 'lg')){
			font-size:34px;
    	}
	}

	.h3{
		font-size:24px;
	    letter-spacing: 3px;
	    max-width: 90%;
		margin-left: auto;
		margin-right: auto;
	}

	.h4{
		font-size:18px;
	    letter-spacing: 3px;
	    max-width: 90%;
		margin-left: auto;
		margin-right: auto;
	}
	p{

    	@media (min-width: map-get($grid-breakpoints, 'lg')){
			max-width: 80%;
			margin-left: auto;
			margin-right: auto;
    	}
    	@media (min-width: map-get($grid-breakpoints, 'lg')){
			max-width: 60%;
    	}
	}
}